.wp-block-latest-posts{list-style-type:none;display:flex;padding:0;margin:40px -20px 20px -20px}.wp-block-latest-posts li{display:flex;flex:1;flex-direction:column;margin:0 20px}.wp-block-latest-posts li a{flex:0 0 40px;order:1;padding-bottom:24px;font-size:1.1em;font-weight:bold;color:#000}.wp-block-latest-posts li a:link{text-decoration:none}.wp-block-latest-posts li time{flex:0;order:2;padding-bottom:20px}.wp-block-latest-posts li .wp-block-latest-posts__featured-image{flex:0;order:3;padding-bottom:30px}.wp-block-latest-posts li .wp-block-latest-posts__post-excerpt{flex:1;order:4}
