body{color:#000;font-family:"Lato",sans-serif;font-size:14px;width:100%;height:100%;margin:0}.wp-block-image{margin:0}.wp-block-columns{display:flex;margin:75px -20px}@media (max-width: 860px){.wp-block-columns{margin:50px 0}}.wp-block-column{flex:1;margin:0 20px}@media (max-width: 764px){.wp-block-columns.has-3-columns{flex-flow:row wrap}.has-3-columns .wp-block-column:first-child{flex-basis:100%}}@media (max-width: 478px){.wp-block-columns.has-3-columns{display:block}.wp-block-column{margin:20px 0}}.wp-block-image figure{margin:0}.wp-block-image figure img{height:auto;max-width:100%}figcaption{color:#666}@media (max-width: 860px){.toggle-text .hide-sm{display:none}}.toggle-text .show-toggle,.toggle-text .hide-toggle{color:#bf322f;display:none;user-select:none;cursor:pointer}@media (max-width: 860px){.toggle-text .show-toggle,.toggle-text .hide-toggle{display:inline}}
